.home_gallery{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding:50px;
    background-color: #F7F7F7;
    border-radius: 50px;
    gap: 30px;  /*permet d'espacer les card tout en concernvant justifiy-c en center*/
}

.cardMother{
    width: 30%;
}





/*--------------------responsive------------------------------*/
@media screen and (max-width: 768px) {

        .home_gallery{
            padding: 0;
        }

        .cardMother{
            width: 100%;
    }
    }

    @media screen and (max-width: 480px) {
        .cardMother a{
            display: flex;
            flex-direction: column;
            height: 200px;
    }
}

/*--------------------responsive------------------------------*/