.totalCollapsible.column {
  flex-direction: column;
}



.customStyle {
 text-align: center;
  width: 80%; 
  margin: auto;
  margin-top: 20px;
}


.collapsible {
  
  height: auto;
    background-color: rgba(255, 96, 96, 1);
    color: #444;
    cursor: pointer;
    padding: 14px;
    margin: auto;
  display: flex;

    text-align: left;
    font-size: 13px;
    color: white;
    border-radius: 5px;
    justify-content: space-between;
    align-items: center;
  }

  .collapsible h2{
    margin: 0;
  }
  
   
  
  /* Style the collapsible content. Note: hidden by default */
  .content {
    
    max-height: 0;
    overflow: hidden;
  transition: max-height 0.7s ease-out;
     
    padding: 0 18px;
    background-color: #f1f1f1;
    color: rgb(255, 144, 144);
    font-size: 1.5rem; 
  }

  .content.active {
    max-height: 500px; 
    transition: max-height 0.3s ease-in;
    height: auto;
    padding: 0;
    margin: auto;
    
    display: flex;
  
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  .incone{
    width: 20%;
    color: beige;
  }

  .test{
    background-color: red;
  }

/*--------------------responsive------------------------------*/

  @media screen and (max-width: 480px) {

    .totalCollapsible.row {
      flex-direction: column;
    }


    .content{
      font-size: 1rem;
    }
  

    .totalCollapsible{
        width: 100%;
        align-items: start;
    }

    .blocCollapsible{
        width: 100%;
    }
  }

  /*--------------------responsive------------------------------*/